<template>
  <el-form class="personal-form">
    <!-- 编辑用户 -->
    <el-form-item label="登录账号" prop='account'>
      <el-input v-model="editForm.account" auto-complete="off" disabled></el-input>
    </el-form-item>
    <el-form-item label="姓名" prop="name">
      <el-input v-model="editForm.name" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="邮箱" prop="email">
      <el-input v-model="editForm.email" auto-complete="off" type="email"></el-input>
    </el-form-item>
    <el-form-item label="固定电话" prop="phone">
      <el-input v-model="editForm.phone" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item label="手机" prop="mobile">
      <el-input v-model="editForm.mobile" auto-complete="off"></el-input>
    </el-form-item>
    <el-button size="medium" @click="saveEditDialog('ruleEditForm')" class="dialog_button" type="primary">提 交</el-button>
  </el-form>

</template>

<script>
import { getValue, changeTree, difference, extend } from "@/util/util.js";
export default {
  name: "userOperation",
  data() {
    return {
      userInfo: {},
      editForm: {
        account: "",
        name: "",
        email: "",
        phone: "",
        mobile: ""
      },
      editRules: this.eachValidate({
        account: { required: true, maxString: 50 },
        name: { required: true, maxString: 50 },
        email: { email: true, maxString: 50 },
        mobile: { mobile: true, maxString: 20 },
        phone: { phone: true, maxString: 13 }
      })
    };
  },

  methods: {
    //保存用户资料
    saveEditDialog() {
      /*   var obj = {};
      for (var item in this.editForm) {
        if (
          item == "departmentName" ||
          item == "orgName" ||
          item == "sex" ||
          item == ""
        ) {
          delete this.editForm[item];
        } else if (this.editForm[item] == undefined) {
          delete this.editForm[item];
        }
      }
      for (var item in this.additionalEditForm) {
        if (this.additionalEditForm[item] == "") {
          this.additionalEditForm[item] = null;
        }
      }
      obj = extend({}, this.editForm); */
      this.userInfo.account =
        this.editForm.account == null ? null : this.editForm.account;
      this.userInfo.name =
        this.editForm.name == null ? null : this.editForm.name;
      this.userInfo.email =
        this.editForm.email == null ? null : this.editForm.email;
      this.userInfo.telephone =
        this.editForm.phone == null ? null : this.editForm.phone;
      this.userInfo.mobile =
        this.editForm.mobile == null ? null : this.editForm.mobile;
      console.log(this.userInfo);
      this.$ajax({
        method: "put",
        url: "rest/user",
        data: this.userInfo
      })
        .then(res => {
          if (res.data.status) {
            this.$message({
              message: res.data.answerMsg,
              type: "success"
            });
          } else {
            this.$message.error(res.data.answerMsg);
          }
        })
        .catch(err => {
          console.log(err, "编辑错误");
        });
    },
    requesetUsers() {
      return this.$ajax({
        method: "get",
        url: "rest/user/currentUser",
        params: {}
      })
        .then(res => {
          if (res.data.status) {
            console.log(res.data.records);
            this.editForm.account =
              res.data.records.account !== null ? res.data.records.account : "";
            this.editForm.name =
              res.data.records.name !== null ? res.data.records.name : "";
            this.editForm.email !== null ? res.data.records.email : "";
            this.editForm.phone =
              res.data.records.phone !== null ? res.data.records.telephone : "";
            this.editForm.mobile =
              res.data.records.mobile !== null ? res.data.records.mobile : "";
            this.userInfo = res.data.records;
          }
        })
        .catch(err => {});
    }
  },

  mounted() {
    this.requesetUsers();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only  -->
<style>
.personal-form {
  width: 500px;
  margin: 30px auto 10px;
}
.personal_div_main {
  width: 400px;
  margin: 0 auto;
}
.personal_div_main input {
  width: 300px;
}
.personal_div_main .el-form-item__label {
  min-width: 88px;
}
.personal_div_main .base-selectTree {
  width: 300px !important;
}
.personal_input_describe {
  width: 300px;
}
.personal_btn {
  margin-left: 295px;
}
.personal_div_pwd .el-input {
  display: inline;
}
</style>




